<template>
    <g>
        <rect :x="x1 * string_spacing" :y="(y1 * frets_spacing + frets_spacing / 2)" :width="(x2 - x1) * string_spacing" :height="(y2 - y1) *  frets_spacing" style="fill: rgba(46, 213, 115, 0.1)"></rect>

        <!-- top left -->
        <polygon
                :transform="'translate(' + (x1 * string_spacing) + ', ' + (y1 * frets_spacing + frets_spacing / 2) + ')'"
                v-show="true"
                points="0,0 10,0 10,4, 4,4 4,10, 0,10"
                fill="green"
                class="resize tl"
                @mousedown="startDrag"
                x="x1" y="y1"
        >
        </polygon>

        <!-- top right -->
        <polygon
                :transform="'translate(' + (x2 * string_spacing - 10) + ', ' + (y1 * frets_spacing + frets_spacing / 2) + ')'"
                v-show="true"
                points="0,0 10,0 10,10, 6,10 6,4 0,4"
                fill="green"
                class="resize tr"
                @mousedown="startDrag"
                x="x2" y="y1">
        </polygon>

        <!-- bottom left -->
        <polygon
                :transform="'translate(' + (x1 * string_spacing) + ', ' + (y2 * frets_spacing + frets_spacing / 2 - 10) + ')'"
                v-show="true"
                points="0,0 4,0 4,6 10,6 10,10 0,10"
                fill="green"
                class="resize bl"
                @mousedown="startDrag"
                x="x1" y="y2">
        </polygon>

        <!-- bottom right -->
        <polygon
                :transform="'translate(' + (x2 * string_spacing - 10) + ', ' + (y2 * frets_spacing + frets_spacing / 2 - 10) + ')'"
                v-show="true"
                points="6,0 10,0 10,10 0,10 0,6 6,6"
                fill="green"
                class="resize br"
                @mousedown="startDrag"
                x="x2" y="y2">
        </polygon>

    </g>
</template>

<script>

    import {mapState} from 'vuex'

    export default {
        name: "Resizer",
        data: function () {
            return {
                x1: 0,
                y1: 0,
                x2: 0,
                y2: 0,
                dragged: null,
                pt: null
            }
        },
        methods: {
            startDrag(e) {
                this.dragged = e.target;
                this.workzone.addEventListener('mousemove', this.onMove, false);
                this.workzone.addEventListener('mouseup', this.stopDrag, false);
            },
            onMove(e) {
                let pos = this.cursorPoint(e);
                this[this.dragged.getAttribute('x')] = Math.floor(pos.x / this.string_spacing);
                this[this.dragged.getAttribute('y')] = Math.floor((pos.y - this.frets_spacing / 2) / this.frets_spacing);
                //this.y1 = pos
            },
            stopDrag(e) {
                this.workzone.removeEventListener('mousemove', this.onMove, false);
                this.workzone.removeEventListener('mouseup', this.stopDrag, false);

                this.$store.dispatch('changeZone', {
                    'x1': this.x1,
                    'x2': this.x2,
                    'y1': this.y1,
                    'y2': this.y2,
                });
            },
            cursorPoint(e) {
                this.pt.x = e.clientX;
                this.pt.y = e.clientY;
                return this.pt.matrixTransform(this.workzone.getScreenCTM().inverse());
            }
        },
        mounted() {
            this.pt = document.getElementById('tablature').createSVGPoint();
            this.x1 = this.$store.state.zone.x1;
            this.y1 = this.$store.state.zone.y1;
            this.x2 = this.$store.state.zone.x2;
            this.y2 = this.$store.state.zone.y2;
        },
        computed: {
            ...mapState(['frets_spacing', 'string_spacing', 'padding', 'marker_width', 'marker_height', 'text_height', 'number_string', 'number_frets']),
            workzone() {
                return document.getElementById('workzone');
            }
        }
    }
</script>

<style scoped>
    .tl, .br {
        cursor: nwse-resize;;
    }

    .bl, .tr {
        cursor: nesw-resize;
    }
</style>